import { Component, OnInit } from '@angular/core';
import { ImagesService } from '../../services/get-images.service';
// import { Iimage } from '../image';
import { ActivatedRoute } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import { error } from 'selenium-webdriver';
import { GetCommentsService } from '../../services/comments/get-comments.service';

@Component({
  selector: 'app-view-photo',
  templateUrl: './view-photo.component.html',
  styleUrls: ['./view-photo.component.css'],
  providers: [ ImagesService ]
})
export class ViewPhotoComponent implements OnInit {

  constructor(private imageService: ImagesService,
              private route: ActivatedRoute) { }

  imageId: number;
  image: any;
  user: any;
  errorMessage;
  loading: boolean = true;

  
  ngOnInit() {
    //get image:
    this.route.params.subscribe(param => this.imageId = Number(param['id']));
    
    //make request and subscribe to observable
    this.imageService.getImage(this.imageId)
      .subscribe(
        image =>
        {
          this.image = image['photo'];
          
          //get user:
          this.user = this.image['user'];

          //hide loader:
          this.loading = false;
          
        },
      error => this.errorMessage = <any>error)
    }
    sendSubscribeMail()
    {
    }

}



