import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/observable/throw';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/do';
import { initDomAdapter } from '@angular/platform-browser/src/browser';

@Injectable()
export class ImagesService {

  private url = 'https://api.500px.com/v1/';

  private photos = 'photos?';
  private photo = 'photos/';
  private _search = "photos/search?term=";
  constructor(private http: HttpClient) { }

  getImages(page: number): Observable<any>
  {
    return this.http.get<any>(this.url + this.photos, {
      params: {
        page: page.toString(),
        consumer_key: 'd2668CjrDpLSiF6a3DPcq06Ryb0NYTIpni36B0Li',
        feature: 'popular',
        image_size: '440'
      }
    })
      .do(data => {
        return JSON.stringify(<object>data);
      })
      .catch(this.handleRequestError);
  }

  getImage(id: number): Observable<any>
  {
    return this.http.get<any>(this.url + this.photo + id.toString() + '?', {
      params : {
        consumer_key: 'd2668CjrDpLSiF6a3DPcq06Ryb0NYTIpni36B0Li',
        image_size: '2048',
        sort: 'rating',
        sort_direction : 'desc'
      }
    })
      .do(image => {
        return JSON.stringify(<object>image);
      })
      .catch(this.handleRequestError)
  }

  search(term: string, page: number)
  {
    return this.http.get<any>(this.url + this._search, {
      params: {
        page: page.toString(),
        consumer_key: 'd2668CjrDpLSiF6a3DPcq06Ryb0NYTIpni36B0Li',
        term: term,
        image_size: '440'        
      }
    })
    .do(images => {
      return JSON.stringify(<object>images);
    })
    .catch(this.handleRequestError)
  }

  private handleRequestError(err: HttpErrorResponse)
  {
    console.log(err.message);
    return Observable.throw(err.message);
  }

}



