import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { HttpClientModule, HttpClient } from '@angular/common/http';
import { GetCommentsService } from '../services/comments/get-comments.service';
import { error } from 'selenium-webdriver';

@Component({
  selector: 'comments-form',
  templateUrl: './comments.component.html',
  styleUrls: ['./comments.component.css'],
  providers: [GetCommentsService],
  
})
export class CommentsComponent implements OnInit {

  commentsURL = '../../assets/comments.json';

  commentsForm: FormGroup;
  photoId: String;
  comments = [];
  numOfComments: number;
  page: number = 1;
  total_pages: number;


  constructor(private formBuilder: FormBuilder,
              private http: HttpClient,
              private commentsService: GetCommentsService)
  {
    this.commentsForm = formBuilder.group({
      'name': [null, Validators.compose([Validators.required, Validators.minLength(3)])],
      'body': [null, Validators.compose([Validators.required, Validators.minLength(5)])],
      'id': [Validators.required]
    })
  }

  ngOnInit() {
    this.photoId = location.pathname.split('/')[location.pathname.split.length]

    this.getComments();
  }

  getComments()
  {
    //check for end of pages:
    if(this.page > this.total_pages) return;

    this.commentsService.getComments(this.photoId, this.page)
      .subscribe(
        comments => {
          //get comments
            comments.comments.forEach(element => {
              this.comments.push(element)
            });

          //get total number of comments
          this.numOfComments = comments.total_items

          //get the number of all pages
          this.total_pages = comments.total_pages;
          
          //change page
          this.page++;
        },
        error => console.log("Sorry, there was a problem with the back-end API")
      )
  }

  addComment(comment)
  {
    var newComment: object = {
      'user': {
        'firstname': comment.name,
        'lastname': '',
      },
      'created_at': new Date,
      'body': comment.body,
      'id': this.photoId
    };

    //TODO: Make post request with the comment, instead of logging it in the console
    this.comments.push(newComment);
    this.numOfComments++;
  }

  onScroll()
  {
    this.getComments();
  }
}



