import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { Observable } from 'rxjs/Observable';

@Injectable()
export class GetCommentsService {

  constructor(private http: HttpClient) { }

  private url: string = 'https://api.500px.com/v1/';
  private endpoint: string = 'photos/';
  private comments = '/comments';

  getComments(id: String, page: Number)
  {
    return this.http.get<any>(this.url + this.endpoint + id + this.comments, {
      params: {
        consumer_key: 'd2668CjrDpLSiF6a3DPcq06Ryb0NYTIpni36B0Li',
        page: page.toString()
      }
    })
      .do(data => {
        return JSON.stringify(<object>data);
      })

      .catch(this.handleRequestError)
  }

  private handleRequestError(err: HttpErrorResponse)
  {
    console.log(err.message);
    return Observable.throw(err.message);
  }  
}



