import { Component, OnInit } from '@angular/core';
import { ImagesService } from '../../services/get-images.service';
import { Observable } from 'rxjs/Observable';
import { Location } from '@angular/common';
import { debug } from 'util';

declare var $: any;

@Component({
  selector: 'app-photos',
  templateUrl: './photos.component.html',
  styleUrls: ['./photos.component.css'],
  providers: [ ImagesService ]
})
export class PhotosComponent implements OnInit {

  constructor(private imageService: ImagesService,
              private location: Location
            ){}
  
  images: any = [];
  errorMessage;
  page = 1;
  maxPage: number;
  
  ngOnInit()
  {
    this.serviceDecorator();
  }
  
  onScroll()
  {
    this.imageService.getImages(this.page)
  }

  serviceDecorator()
  {
    if(this.maxPage < this.page) return;

    this.imageService.getImages(this.page)
    .subscribe(
      images => {
        
        if(this.page > 1)
        {
          images.photos.forEach(element => {
            this.images.push(element);
          });
        }
        else
        {
          this.images = images.photos;
        }
        
        //set next page
        this.page++;

        //get the number of pages with results
        this.maxPage = images.total_pages;
        },
        error => this.errorMessage = <any>error)
  }
}



