import { Component, OnInit } from '@angular/core';
import { ImagesService } from '../services/get-images.service';
// import { Iimage } from '../photos/image';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import {Router} from "@angular/router";
import { ActivatedRoute } from '@angular/router';
import { error } from 'selenium-webdriver';

declare var $:any;

@Component({
  selector: 'app-search',
  templateUrl: './search.component.html',
  styleUrls: ['./search.component.css'],
  providers: [ImagesService]
})
export class SearchComponent implements OnInit {

  constructor(private imageService: ImagesService,
              private router: Router,
              private route: ActivatedRoute,
              private formBuilder: FormBuilder,)
  {
    this.rForm = formBuilder.group({
      'key': [null, Validators.compose([Validators.required, Validators.minLength(3)])],
      'author' : ''
    })
  }
  
  searchKey: string;
  lastSearchKey:string;
  authorKey: string;
  images: any = [];
  errorMessage;
  page: number = 1;
  totalPages: number;
  loading: boolean = true;

  imagesSubscription;

  rForm: FormGroup;// form

  ngOnInit() {
    this.imagesSubscription = this.route.params.subscribe(
      params => {
        this.searchKey = params.key;
        this.authorKey = params.author;

        if(this.searchKey === '')
        {
          this.imageService.getImages(this.page)
            .subscribe(
              images => {
                this.images = images;
              
                //next page
                this.page ++;
              },
              error => this.errorMessage = <any>error
            )
            return;
        }//load editors choice
        else
        {
          this.subscribeToImagesService();
        }
      }
    )
    
  }

  subscribeToImagesService()
  {
    this.imageService.search(this.searchKey, this.page)
    .subscribe(
      images => {

        if(this.totalPages < this.page)
        {
          this.loading = false;
          return;
        }

        if(images.total_pages === 1)
        {
          this.loading = false;
        }
        
          images.photos.forEach(element => {
            this.images.push(element);
          });

        //change page
        this.page++;

        //get all pages
        this.totalPages = images.total_pages;

        //save last search
        this.lastSearchKey = this.searchKey;
      },
      error => this.errorMessage = <any>error);
  }

  makeSearch()
  {
    this.subscribeToImagesService();
  }

  search(search)
  {
    this.images = [];
    this.page = 1;
    this.totalPages = 1;
    this.router.navigate([`/search/${search.key}/${search.author}`]);
    
  }
}



