import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { HttpClientModule } from '@angular/common/http';


import { AppComponent } from './app.component';
import { NavComponent } from './Layouts/nav/nav.component';
import { WelcomeComponent } from './Layouts/welcome/welcome.component';
import { FooterComponent } from './Layouts/footer/footer.component';
import { PhotoComponent } from './photos/photo/photo.component';
import { PhotosComponent } from './photos/photos/photos.component';
import { ViewPhotoComponent } from './photos/view-photo/view-photo.component';
import { GoogleMapsComponent } from './google-maps/google-maps.component';
import { SearchComponent } from './search/search.component';
import { CommentsComponent } from './comments/comments.component';
import { SpinnerComponent } from './spinner/spinner.component';

//infinite scroll
import { InfiniteScrollModule } from 'ngx-infinite-scroll';
import { NotFoundComponent } from './Layouts/not-found/not-found.component';

//timeAgo pipe
import {TimeAgoPipe} from 'time-ago-pipe';

@NgModule({
  declarations: [
    AppComponent,
    NavComponent,
    WelcomeComponent,
    FooterComponent,
    PhotoComponent,
    PhotosComponent,
    ViewPhotoComponent,
    GoogleMapsComponent,
    SearchComponent,
    CommentsComponent,
    SpinnerComponent,
    NotFoundComponent,
    TimeAgoPipe
  ],
  imports: [
    BrowserModule,
    FormsModule,
    ReactiveFormsModule,
    HttpClientModule,

    //infinite scroll:
    InfiniteScrollModule,
    
    //Router
    RouterModule.forRoot([
      { path: '', component: WelcomeComponent },
      { path: 'top-photos', component: PhotosComponent },
      { path: 'photo/:id', component: ViewPhotoComponent },
      { path: 'search', component: SearchComponent },      
      { path: 'search/:key', component: SearchComponent },
      { path: 'search/:key/:author', component: SearchComponent },
      { path: '404', component: NotFoundComponent },
      { path: '**', redirectTo:'/404', pathMatch: 'full' }
    ]),
    
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }



